body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.form-container {
    background-image: url('https://www.corporatevision-news.com/wp-content/uploads/2021/12/Inventory.jpg');
    background-size: cover; /* Ensure the image covers the whole background */
    background-repeat: no-repeat;
    background-position: center;
    min-height: 100vh; /* Make sure it takes at least the full viewport height */
    padding: 20px; /* Add some padding */
    color: white; /* Change text color to improve visibility */
}

.form-title {
    text-align: center;
    margin-bottom: 20px;
}

.item-form {
    background-color: rgba(0, 0, 0, 0.7); /* Add a dark overlay for better contrast */
    padding: 20px;
    border-radius: 8px; /* Rounded corners */
}

.form-label {
    display: block;
    margin-bottom: 8px;
}

.form-input,
.form-textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form-button {
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
}

.form-button:hover {
    background-color: #0056b3; /* Darken on hover */
}

.list-title {
    margin-top: 40px;
    text-align: center;
}

.item-list {
    list-style-type: none;
    padding: 0;
}

.list-item {
    background-color: rgba(255, 255, 255, 0.8); /* Light background for list items */
    margin: 10px 0;
    padding: 10px;
    border-radius: 4px;
}

.button-group {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.edit-button,
.delete-button {
    background-color: #ffc107; /* Bootstrap warning color */
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
}

.edit-button:hover {
    background-color: #e0a800; /* Darken on hover */
}

.delete-button {
    background-color: #dc3545; /* Bootstrap danger color */
}

.delete-button:hover {
    background-color: #c82333; /* Darken on hover */
}
